<template>
  <div class="page">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="绑定微信"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content bind-weixin">
      <div class="time-note">一个微信号单月内最多可以绑定两次</div>
      <div class="step-mark"><div></div><div>步骤一</div></div>
      <div class="guide">长按并存储二维码图片，打开微信扫一扫识别该二维码找到公众号—（或微信搜索“qiankakefu”）。</div>
      <div class="qrcode">
         <img src="//qianka.b0.upaiyun.com/images/3f649daeb3e028af33de0e26d23a96cf.png">
      </div>
      <div class="step-mark"><div></div><div>步骤二</div></div>
      <div class="guide guide-2">关注后，点击“绑定帐号”，获取验证码。</div>
      <label for="verify">
        <div class="title">验证码</div>
        <input type="text" id="verify" class="input" placeholder="输入验证码" v-model="verify" maxlength="6">
        <div class="bottom-line"></div>
      </label>

      <button class="button button-submit" :class="{'can-submit': can_submit, 'button-disabled': !can_submit}" @click="submit()">提交</button>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .bind-weixin {
    margin-top: 50px;
    padding-top: 20px;
    padding-left: 30px;
    background: #FFFFFF;

    .time-note {
      background: #FFF8EE;
      text-align: center;
      font-size: 12px;
      color: #3A3A3A;
      line-height: 40px;
      margin: 0 18px 20px;
      width: 280px;
      height: 40px;

      @include font-regular();
    }

    .step-mark {
      margin-bottom: 12px;
      line-height: 18px;
      height: 18px;

      div:first-child {
        display: inline-block;
        margin-right: 8px;
        width: 3px;
        height: 18px;
        line-height: 18px;
        font-size: 13px;
        background: #DDDDDD;
        vertical-align: middle;
      }

      div:nth-child(2) {
        display: inline-block;
        font-weight: 600;
        font-size: 13px;
        height: 18px;
        line-height: 18px;
        min-height: 18px;
        vertical-align: middle;
        color: #3A3A3A;

        @include font-semibold();
      }
    }

    .guide {
      width: 315px;
      font-size: 13px;
      color: #3a3a3a;
      margin-bottom: 20px;

      @include font-regular();

      &.guide-2 {
        margin-bottom: 30px;
      }
    }

    .qrcode {
      width: 100px;
      height: 100px;
      margin-bottom: 20px;

      img {
        width: 100px;
        height: 100px;
        -webkit-touch-callout: default;
      }
    }

    .title {
      color: #b5b5b5;
      font-size: 12px;
      height: 17px;
      line-height: 17px;

      @include font-regular();
    }

    .input {
      width: 166px;
      height: 33px;
      font-size: 24px;
      line-height: 33px;
      margin-bottom: 3px;
      display: inline-block;
      padding: 0;

      @include font-regular();

      &::-webkit-input-placeholder {
        line-height: 33px;
        height: 33px;
        color: #DDDDDD;

        @include font-regular();
      }
    }

    .bottom-line {
      width: 200px;
      height: 1px;
      background: #DDDDDD;
      margin-bottom: 35px;

      &.last {
        margin-bottom: 60px;
      }
    }

    .button-submit {
      width: 172px;
      height: 40px;
      min-height: 40px;
      line-height: 40px;
      font-size: 13px;
      font-weight: 500;
      color: #FFFFFF;
      margin-bottom: 206px;
      border-radius: 2px;

      @include font-semibold();

      &.button-disabled {
        background: #DDDDDD;
      }

      &.can-submit {
        background-color: #ef502b;
      }
    }

  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapActions} from 'vuex'

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        can_submit: false,
        verify: ''
      }
    },

    watch: {
      verify: function (v) {
        if (v.length === 6) {
          this.can_submit = true
        } else {
          this.can_submit = false
        }
      }
    },

    methods: {
      back () {
        let path = _.get(this.$route.query, 'backroute', '/user/profile')
        $router.back({ path })
      },

      submit () {
        if (!this.verify) {
          return
        }
        this.bindWechat(this.verify)
      },

      ...mapActions('user', [
        'bindWechat'
      ])
    }
  }
</script>



